import React, { useState } from "react";
import AddIcon from '@material-ui/icons/Add';

const CreateNote = (props) => {
    const [expand, setExpand] = useState(false);

    const [note, setNote] = useState(
        {
            title: '',
            content: '',
        }
    );

    const inputEvent = (event) => {
        const { name, value } = event.target;

        setNote((prevData) => {
            return {
                ...prevData,
                [name]: value,
            }
        });
        console.log(note);
    }

    const addEvent = () => {
        props.passNote(note);
        setNote({
            title: '',
            content: '',
        })
    }

    const expandIt = () => {
        setExpand(true);
    }

    const backToNormal = ()=>{
        setExpand(false);
    }

    return (
        <>
            <div className="main_note">
                <form className="form">
                    {expand ?
                        <input
                            type='text'
                            placeholder="Title"
                            autoComplete="off"
                            value={note.title}
                            name="title"
                            onChange={inputEvent}

                        /> : null}

                    <textarea
                        rows=''
                        column=''
                        placeholder="Write a note..."
                        value={note.content}
                        name="content"
                        onChange={inputEvent}
                        onClick={expandIt}
                        onDoubleClick={backToNormal}
                    />

                    {expand ?
                        <AddIcon onClick={addEvent} className="plus_sign"
                        /> : null}
                </form>
            </div>
        </>
    )
}

export default CreateNote;
